// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 
import Vue from 'vue'

import {
    Popover,
    Button,
    Upload,
    Dialog
} from 'element-ui';
Vue.use(Popover)
Vue.use(Button)
Vue.use(Upload)
Vue.use(Dialog)

import axios from 'axios'     // send ajax
import App from './App'       // vue loader App.vue

import Vuex from 'vuex'       // 
import store from './store'   // support of 

import router from './router' //  router , index.js,
import Vuetify from 'vuetify' // 

import './assets/vue_strengthen' //  vue 

// css import 
import './assets/stylesheets/icons.css' // vuetify icons
import './assets/stylesheets/vuetify.min.css' // vuetify css


import './assets/stylesheets/element-ui.index.css' // element-ui

Vue.use(Vuex)
Vue.use(Vuetify)  //  vuetify 

Vue.config.productionTip = false
Vue.prototype.axios = axios  //ajax 

/* eslint-disable no-new */
//  
var MyPost = new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',  
  components: { App } // 
})



